.weui-tabbar {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  position: absolute;
  z-index: 500;
  bottom: 0;
  width: 100%;
  background-color: #F7F7FA;
}
.weui-tabbar:before {
  content: " ";
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  height: 0.026667rem;
  border-top: 0.026667rem solid #C0BFC4;
  color: #C0BFC4;
  -webkit-transform-origin: 0 0;
          transform-origin: 0 0;
  -webkit-transform: scaleY(0.5);
          transform: scaleY(0.5);
}
.weui-tabbar__item {
  display: block;
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  padding: 0.133333rem 0 0;
  font-size: 0;
  color: #999999;
  text-align: center;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
.weui-tabbar__item.weui-bar__item_on .weui-tabbar__icon,
.weui-tabbar__item.weui-bar__item_on .weui-tabbar__icon > i,
.weui-tabbar__item.weui-bar__item_on .weui-tabbar__label {
  color: #5CACEE;
}
.weui-tabbar__icon {
  display: inline-block;
  width: 0.72rem;
  height: 0.72rem;
}
i.weui-tabbar__icon,
.weui-tabbar__icon > i {
  font-size: 0.64rem;
  color: #999999;
}
.weui-tabbar__icon img {
  width: 100%;
  height: 100%;
}
.weui-tabbar__label {
  text-align: center;
  color: #999999;
  font-size: 0.266667rem;
  line-height: 1.8;
}
.weui-tab {
  position: relative;
  height: 100%;
}
.weui-tab__panel {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  height: 100%;
  padding-bottom: 1.333333rem;
  overflow: auto;
  -webkit-overflow-scrolling: touch;
}
.weui-tab__content {
  display: none;
}
.vux-reddot,
.vux-reddot-border,
.vux-reddot-s {
  position: relative;
}
.vux-reddot:after,
.vux-reddot-border:after,
.vux-reddot-s:after {
  content: '';
  position: absolute;
  display: block;
  width: 0.213333rem;
  height: 0.213333rem;
  background-color: #f74c31;
  border-radius: 0.133333rem;
  right: -0.08rem;
  top: -0.08rem;
  background-clip: padding-box;
}
.vux-reddot-border:before {
  content: '';
  position: absolute;
  display: block;
  width: 0.213333rem;
  height: 0.213333rem;
  background-color: #fff;
  border-radius: 0.133333rem;
  right: -0.106667rem;
  top: -0.106667rem;
  background-clip: padding-box;
  padding: 0.026667rem;
}
.vux-reddot-s:after {
  width: 0.16rem;
  height: 0.16rem;
  top: -0.133333rem;
  right: -0.133333rem;
}
.weui-tabbar__icon {
  position: relative;
}
.weui-tabbar__icon > sup {
  position: absolute;
  top: -0.213333rem;
  left: 100%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  z-index: 101;
}
.weui-tabbar__item.vux-tabbar-simple {
  padding: 0;
  height: 1.333333rem;
  line-height: 1.333333rem;
}
.vux-tabbar-simple .weui-tabbar__label {
  font-size: 0.373333rem;
  line-height: 1.333333rem;
