  width: 0.64rem;
  height: 0.64rem;
}
.calendar-year > span.calendar-header-right-arrow,
.calendar-month > span.calendar-header-right-arrow {
  left: auto;
  right: 0;
}
.vux-prev-icon,
.vux-next-icon {
  position: absolute;
  left: 0;
  top: 0.4rem;
  display: inline-block;
  width: 0.32rem;
  height: 0.32rem;
  border: 0.026667rem solid #04BE02;
  border-radius: 0;
