.calendar-year,
.calendar-month {
  position: relative;
}
.calendar-header {
  line-height: 1.066667rem;
  font-size: 1.2em;
  overflow: hidden;
}
.calendar-header > div {
  float: left;
  width: 50%;
  text-align: center;
  overflow: hidden;
}
.calendar-header span:last-of-type {
  float: right;
  vertical-align: bottom;
}
.switch-btn,
.calendar-title {
  display: inline-block;
  border-radius: 0.106667rem;
  line-height: 0.8rem;
}
.switch-btn {
  width: 0.8rem;
  margin: 0.133333rem;
  color: #39b5b8;
  font-family: "SimSun";
}
.calendar-title {
  padding: 0 6%;
  color: #333;
}
.switch-btn:active,
.calendar-title:active,
.calendar-header a.active {
  background-color: #39b5b8;
  color: #fff;
}
.calendar-week {
  overflow: hidden;
}
.calendar-week span {
  float: left;
  width: 14.28%;
  font-size: 1.6em;
  line-height: 0.906667rem;
  text-align: center;
}
.inline-calendar {
  width: 100%;
  background-color: #fff;
  border-radius: 0.053333rem;
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease;
}
.inline-calendar td.is-today,
.inline-calendar td.is-today.is-disabled {
  color: #04BE02;
}
.calendar-enter,
.calendar-leave-active {
  opacity: 0;
  -webkit-transform: translate3d(0, -0.266667rem, 0);
          transform: translate3d(0, -0.266667rem, 0);
}
.calendar:before {
  position: absolute;
  left: 0.8rem;
  top: -0.266667rem;
  content: "";
  border: 0.133333rem solid rgba(0, 0, 0, 0);
  border-bottom-color: #DEDEDE;
}
.calendar:after {
  position: absolute;
  left: 0.8rem;
  top: -0.24rem;
  content: "";
  border: 0.133333rem solid rgba(0, 0, 0, 0);
  border-bottom-color: #fff;
}
.inline-calendar table {
  clear: both;
  width: 100%;
  border-collapse: collapse;
  color: #444444;
}
.inline-calendar td {
  padding: 0.133333rem 0;
  text-align: center;
  vertical-align: middle;
  font-size: 0.426667rem;
  position: relative;
}
.inline-calendar td.week {
  pointer-events: none !important;
  cursor: default !important;
}
.inline-calendar td.is-disabled {
  color: #c0c0c0;
}
.inline-calendar td > span.vux-calendar-each-date {
  position: relative;
  display: inline-block;
  width: 0.693333rem;
  height: 0.693333rem;
  line-height: 0.666667rem;
  border-radius: 50%;
  text-align: center;
  border: 0.026667rem solid transparent;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.inline-calendar td.current > span.vux-calendar-each-date {
  background-color: #04BE02;
  color: #fff !important;
}
.inline-calendar th {
  color: #000;
  font-weight: normal;
}

