.vux-header {
  position: relative;
  padding: 0.08rem 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  background-color: #35495e;
}
.vux-header .vux-header-title {
  line-height: 1.066667rem;
  text-align: center;
  font-size: 0.48rem;
  font-weight: 400;
  color: #fff;
}
.vux-header-title-area,
.vux-header .vux-header-title {
  margin: 0 2.346667rem;
  height: 1.066667rem;
  width: auto;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.vux-header .vux-header-title > span {
  display: inline-block;
}
.vux-header .vux-header-left,
.vux-header .vux-header-right {
  position: absolute;
  top: 0.373333rem;
  display: block;
  font-size: 0.373333rem;
  line-height: 0.56rem;
  color: #ccc;
}
.vux-header .vux-header-left a,
.vux-header .vux-header-left button,
.vux-header .vux-header-right a,
.vux-header .vux-header-right button {
  float: left;
  margin-right: 0.213333rem;
  color: #ccc;
}
.vux-header .vux-header-left a:active,
.vux-header .vux-header-left button:active,
.vux-header .vux-header-right a:active,
.vux-header .vux-header-right button:active {
  opacity: 0.5;
}
.vux-header .vux-header-left {
  left: 0.48rem;
}
.vux-header .vux-header-left .vux-header-back {
  padding-left: 0.426667rem;
}
.vux-header .vux-header-left .left-arrow {
  position: absolute;
  width: 0.8rem;
  height: 0.8rem;
  top: -0.133333rem;
  left: -0.133333rem;
}
.vux-header .vux-header-left .left-arrow:before {
  content: "";
  position: absolute;
  width: 0.32rem;
  height: 0.32rem;
  border: 0.026667rem solid #ccc;
  border-width: 0.026667rem 0 0 0.026667rem;
  -webkit-transform: rotate(315deg);
          transform: rotate(315deg);
  top: 0.213333rem;
  left: 0.186667rem;
}
.vux-header .vux-header-right {
  right: 0.4rem;
}
.vux-header .vux-header-right a,
.vux-header .vux-header-right button {
  margin-left: 0.213333rem;
  margin-right: 0;
}
.vux-header .vux-header-right .vux-header-more:after {
  content: "\2022   \2022   \2022   ";
  font-size: 0.426667rem;
}
.vux-header-fade-in-right-enter-active {
  -webkit-animation: fadeinR 0.5s;
          animation: fadeinR 0.5s;
}
.vux-header-fade-in-left-enter-active {
  -webkit-animation: fadeinL 0.5s;
          animation: fadeinL 0.5s;
}
@-webkit-keyframes fadeinR {
0% {
    opacity: 0;
    -webkit-transform: translateX(4rem);
            transform: translateX(4rem);
}
100% {
    opacity: 1;
    -webkit-transform: translateX(0);
            transform: translateX(0);
}
}
@keyframes fadeinR {
0% {
    opacity: 0;
    -webkit-transform: translateX(4rem);
            transform: translateX(4rem);
}
100% {
    opacity: 1;
    -webkit-transform: translateX(0);
            transform: translateX(0);
}
}
@-webkit-keyframes fadeinL {
0% {
    opacity: 0;
    -webkit-transform: translateX(-4rem);
            transform: translateX(-4rem);
}
100% {
    opacity: 1;
    -webkit-transform: translateX(0);
            transform: translateX(0);
}
}
@keyframes fadeinL {
0% {
    opacity: 0;
    -webkit-transform: translateX(-4rem);
            transform: translateX(-4rem);
}
100% {
    opacity: 1;
    -webkit-transform: translateX(0);
            transform: translateX(0);
}
}
